import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

//imports
import { HomeComponent } from './pages/home/home.component';
import { AboutComponent } from './pages/about/about.component';
import { ContactComponent } from './pages/contact/contact.component';
import { ContactFormComponent } from './pages/contact/contact-form.component';
import { BioComponent } from './pages/bio/bio.component';
import { MiscComponent } from './pages/misc/misc.component';
import { BooksComponent } from './pages/books/books.component';
import { ResumeComponent } from './pages/about/resume/resume.component';
import { BulletinComponent } from './pages/bulletin/bulletin.component';

const routes: Routes = [
  {path: 'home', component: HomeComponent },
  {path: 'about', component: AboutComponent },
  {path: 'contact', component: ContactComponent },
  {path: 'contactform', component: ContactFormComponent },
  {path: 'bio', component: BioComponent },
  {path: 'misc', component: MiscComponent },
  {path: 'books', component: BooksComponent },
  {path: 'resume', component: ResumeComponent },
  {path: 'bulletin', component: BulletinComponent },
  {path: '', redirectTo:'/home', pathMatch: 'full'}
];

@NgModule({
  imports: [ RouterModule.forRoot(routes)],
  exports: [
    RouterModule
  ]
})

export class AppRoutingModule { }



