import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';

import { AppComponent } from './app.component';
import { AppRoutingModule } from './/app-routing.module';
import { HomeComponent } from './pages/home/home.component';
import { AboutComponent } from './pages/about/about.component';
import { ContactComponent } from './pages/contact/contact.component';
import { ContactFormComponent } from './pages/contact/contact-form.component';
import { NavbarComponent } from './navbar/navbar.component';

import { AngularFireModule } from 'angularfire2';
import { AngularFirestoreModule } from 'angularfire2/firestore';
import { AngularFireStorageModule } from 'angularfire2/storage';
import { environment } from '../environments/environment';
import { SubmittedComponent } from './pages/contact/submitted.component';
import { ResumeComponent } from './pages/about/resume/resume.component';
import { BioComponent } from './pages/bio/bio.component';
import { MiscComponent } from './pages/misc/misc.component';
import { BooksComponent } from './pages/books/books.component';

import {NgbModule} from '@ng-bootstrap/ng-bootstrap';
import { LoadingComponent } from './util/loading.component';
import { BulletinComponent } from './pages/bulletin/bulletin.component';


@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    AboutComponent,
    ContactComponent,
    ContactFormComponent,
    NavbarComponent,
    SubmittedComponent,
    ResumeComponent,
    BioComponent,
    MiscComponent,
    BooksComponent,
    LoadingComponent,
    BulletinComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    FormsModule,
    AngularFireModule.initializeApp(environment.firebase),
    AngularFirestoreModule,
    AngularFireStorageModule,
    NgbModule.forRoot()
  ],
  providers: [],
  bootstrap: [AppComponent]
})

export class AppModule {

}



