import { Component, OnInit } from '@angular/core';
import { Book } from './../../util/models/book';
import { LoadingComponent } from './../../util/loading.component';

import { AngularFirestore, AngularFirestoreCollection, AngularFirestoreDocument } from 'angularfire2/firestore';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';

@Component({
  selector: 'app-books',
  templateUrl: './books.component.html',
  styleUrls: ['./books.component.css']
})
export class BooksComponent implements OnInit {

  fsBookList: AngularFirestoreCollection<Book>;
  bookList: Observable<Book[]>;
  loading: boolean = true;

  constructor(private db: AngularFirestore) { }

  ngOnInit(){
    this.fsBookList = this.db.collection('books', ref => ref.orderBy('Rating', 'desc'));
    this.bookList = this.fsBookList.valueChanges();
    this.bookList.subscribe(()=> this.loading = false);
  }

  byTitle(){
    this.fsBookList = this.db.collection('books', ref => ref.orderBy('Title'));
    this.bookList = this.fsBookList.valueChanges();
    this.bookList.subscribe(()=> this.loading = false);
  }

  byAuthor(){
    this.fsBookList = this.db.collection('books', ref => ref.orderBy('Author'));
    this.bookList = this.fsBookList.valueChanges();
    this.bookList.subscribe(()=> this.loading = false);
  }

  byRating(){
    this.fsBookList = this.db.collection('books', ref => ref.orderBy('Rating', 'desc'));
    this.bookList = this.fsBookList.valueChanges();
    this.bookList.subscribe(()=> this.loading = false);
  }

}



