import { Component, OnInit } from '@angular/core';
import { Bulletin } from './../../util/models/bulletin';
import { LoadingComponent } from './../../util/loading.component';

import { AngularFirestore, AngularFirestoreCollection, AngularFirestoreDocument } from 'angularfire2/firestore';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';

@Component({
  selector: 'app-bulletin',
  templateUrl: './bulletin.component.html',
  styleUrls: ['./bulletin.component.css']
})
export class BulletinComponent implements OnInit {

  fsBulletinList: AngularFirestoreCollection<Bulletin>;
  bulletinList: Observable<Bulletin[]>;
  loading: boolean = true;

  constructor(private db: AngularFirestore) { }

  ngOnInit() {
    this.fsBulletinList = this.db.collection('bulletin');
    this.bulletinList = this.fsBulletinList.valueChanges();
    this.bulletinList.subscribe(()=> this.loading = false);
  }

}



